<template>
	<div id="choose-your-counter">
		<div class="uk-container uk-text-center" style="padding: 50px;">
			<h1>CHOOSE YOUR COUNTER NUMBER</h1>
			<div class="uk-child-width-1-3@m uk-grid-small uk-grid-match" uk-grid>
			    <div v-for="counter in counters">
			        <div class="uk-card uk-card-default uk-card-body">
			            <h3 class="uk-card-title" style="font-size: 70px;">{{counter.label}}</h3>
			            <p>OCCUPIED</p>
			        </div>
			    </div>
			</div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
const counters = app.service('counters')
export default {
	data () {
		return {
			counters: []
		}
	},
	async mounted () {
		try {
			await isAuth()
			this.getCounters()
		} catch (error) {
			console.log(error)
		}
	},
	methods: {
		async getCounters () {
			try {
				let result = await counters.find()
				this.counters = result.data
				console.log(result.data)
			} catch (error) {
				console.log(error)
			}
		}
	}
}
</script>


