<template>
  <div id="monitor" style="height: 100vh;">
    <div class="header">
      <div style="color: #fff; margin-top: .3%; margin-bottom: 10px !important; font-size: 30px; font-weight: bold; text-transform: uppercase;">{{ company_name }}</div>
    </div>
    <div class="content">
      <div class="ads">
        <div class="video-ads">
          <video ref="videoRef" width="100%" height="100%" id="myVideo" preload="none" muted>
          </video>
        </div>
        <div style="text-align: center;">
          <h1 style="text-transform: uppercase;">{{ date }}</h1>
        </div>
      </div>
      <div class="counters">
        <div v-for="(counter, index) in counters" :key="index">
          <div class="header" style="background: #fff;">
              <h2 style="font-size: 35px; text-transform: uppercase;">
                  {{ counter.label }}
              </h2>
          </div>
          <div class="body text-center">
              <h1 style="font-size: 80px; margin-top: 0px; margin-bottom: 0px;" :id="counter._id">N/A</h1>
              <hr v-if="index < 2" style="height: 5px; background: #0B5365;">
          </div>
        </div>
      </div>
    </div>
    <div class="announcement">
      <div style="width: 14%;"><strong>ANNOUNCEMENT:</strong></div>
      <div style="width: 86%;"><marquee style="margin-top: 0%;" scrollamount="4">{{ announcement }}</marquee></div>
    </div>
  </div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
import { API_SERVER } from '@/api/url'
export default {
	data () {
		return {
			date: moment().format('LLL'),
      counters: [],
      API_SERVER,
      ads: [],
      ctr: 0,
      company_name: '',
      announcement: ''
		}
	},
  async mounted () {
    this.getCounters()
    this.getNowServing()
    this.getAds()
    this.getSettings()
  },
  methods: {
    async test () {
      document.getElementById('myVideo').addEventListener('ended', (e) => {
        this.ctr++;
          if (this.ctr == (this.ads.length)) {
            this.ctr = 0;
            this.videoPlay(this.ctr);
          } else {
            this.videoPlay(this.ctr);
          }
      }, false)
      this.videoPlay(0)
    },
    async videoPlay(videoNum) {
      document.getElementById("myVideo").setAttribute("src", this.ads[videoNum])
      document.getElementById("myVideo").load()
      this.$refs.videoRef.play()
    },
    async getCounters () {
      try {
        let result = await app.service('counters').find({
          query: {
            $limit: 3
          }
        })
        this.counters = result.data
      } catch (error) {
        console.log(error)
      }
    },
    async getNowServing () {
      try {
        let result = await app.service('now-serving').find()
        result.data.forEach(val => {
          $(`#${val.counter._id}`).text(val.ticket.label)
        })
      } catch (error) {
        console.log(error)
      }
    },
    async getTicket (data) {
      console.log(data)
      try {
        let result = await app.service('tickets').get(data.ticket._id)
        $(`#${data.counter._id}`).text(result.label)
      } catch (error) {
        console.log(error)
      }
    },
    async getAds () {
      try {
        let result = await app.service('advertisements').find()
        result.data.forEach((val) => {
          // Local
          // this.ads.push(`${API_SERVER}/uploads/${val.file_name}`)

          // Cloudinary
          this.ads.push(`${val.file_name}`)
        })
      } catch (error) {
        console.log(error)
      } finally {
        this.test()
      }
    },
    async getSettings () {
      try {
        let result = await app.service('settings').find()
        this.company_name = result.data[0].company_name
        this.announcement = result.data[0].announcement
      } catch (error) {
        console.log(error)
      }
    }
  },
  feathers: {
    'now-serving': {
      created (data) {
        this.getTicket(data)
      }
    }
  }
}
</script>
<style scoped>
#monitor {
  display: flex;
  flex-direction: column;
}
.header {
  background: #0B5365;
  height: 7vh;
  padding-left: 20px;
  text-align: center;
}
.announcement {
  background: #0B5365;
  padding-left: 20px;
  height: 7vh;
  padding-top: 1%;
  font-size: 20px;
  color: #fff;
  display: flex;
  width: 100%;
  padding-right: 20px;
}
.content {
  background: #fff;
  height: 87vh;
  display: flex;
}
.ads {
  width: 60%;
  padding: 20px;
  padding-right: 0px;
  display: flex;
  flex-direction: column;
  max-height: 78vh;
}
.counters {
  width: 40%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  /*height: 90vh;*/
  /*overflow-y: scroll;*/
}
.video-ads {
  /*background: red;*/
  height: 100%;
}
.mid-counter {
  background: #89e8c5 !important;
}
.green > .header {
  background: #4DAE55 !important;
}
.orange > .header {
  background: #FF9729 !important;
}
.blue-gray > .header {
  background: #607D8A !important;
}
</style>


