<template>
	<div id="login">
		<div class="login-page ls-closed">
			<div class="login-box login-page ls-closed" style="margin-bottom: 0px;">
			    <div class="logo">
			        <a href="javascript:void(0);">Admin/Counter</b></a>
			    </div>
			    <div class="card" style="margin-bottom: 0px;">
			        <div class="body">
			            <form @submit.prevent="login">
			                <div class="msg">Login to start your session</div>
			                <div class="input-group">
			                    <span class="input-group-addon">
			                        <i class="material-icons">person</i>
			                    </span>
			                    <div class="form-line">
			                        <input type="text" class="form-control" name="username" placeholder="Email" required="" autofocus="" aria-required="true" aria-invalid="false" v-model="email">
			                    </div>
			                </div>
			                <div class="input-group">
			                    <span class="input-group-addon">
			                        <i class="material-icons">lock</i>
			                    </span>
			                    <div class="form-line">
			                        <input type="password" class="form-control" name="password" placeholder="Password" required="" aria-required="true" aria-invalid="false" v-model="password">
			                    </div>
			                </div>
			                <div class="row">
			                		<div class="col-xs-6"></div>
			                    <div class="col-xs-6">
			                      <button class="btn btn-block bg-primary waves-effect" type="submit">LOGIN</button>
			                    </div>
			                </div>
			            </form>
			        </div>
			    </div>
			</div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
export default {
	data () {
		return {
			email: '',
			password: ''
		}
	},
	mounted () {
		this.checkAuth()
	},
	methods: {
		async login () {
			try {
				let result = await app.authenticate({
					strategy: 'local',
					email: this.email,
					password: this.password
				})
				console.log('result', result)
				if (result.user.type == 'employee') {
					window.location.href = '/counter'
				} else {
					window.location.href = '/admin'
				}
				console.log()
			} catch (err) {
				alert (err.message)
			}
		},
		async checkAuth () {
			try {
				let result = await isAuth()
				console.log(result.user)
			} catch (error) {
				console.log(error)
			}
		}
	}
}
</script>
<style>
#login {
	/*background-image: url('https://i.pinimg.com/originals/cd/c8/de/cdc8de777d6705a71853293304c5436f.jpg');*/
	height: 100vh;
}
.uk-card {
	border-radius: 60px 0 60px 0;
	background: #1a7b9e;
}
button:hover {
	color: #fff;
}
</style>


