<template>
	<div id="tickets">
		<section class="content">
			<div class="container-fluid">
			  <div class="block-header">
					<div class="row clearfix">
					    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					        <div class="card">
					            <div class="header">
					                <h2>
					                    TICKETS
					                </h2>
					                <ul class="header-dropdown m-r--5">
					                    <li class="dropdown">
					                        <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
					                            <i class="material-icons">more_vert</i>
					                        </a>
					                        <ul class="dropdown-menu pull-right">
					                            <li><a data-toggle="modal" data-target="#addCounter" class=" waves-effect waves-block">Generate Tickets</a></li>
					                            <li><a @click="resetQue" class=" waves-effect waves-block">Reset Que</a></li>
					                        </ul>
					                    </li>
					                </ul>
					            </div>
					            <div class="body table-responsive">
					                <table class="table">
					                    <thead>
					                        <tr>
					                            <th>TOTAL NUMBER OF TICKETS</th>
					                        </tr>
					                    </thead>
					                    <tbody>
					                        <tr>
				                            <td>{{ tickets }}</td>
					                        </tr>
					                    </tbody>
					                </table>
					            </div>
					        </div>
					    </div>
					</div>			  
				</div>
			</div>
		</section>
		<!-- Add Counter -->
		<div class="modal fade" id="addCounter" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Generate Ticket</h4>
				  </div>
				  <form @submit.prevent="generateTicket">
					  <div class="modal-body" style="padding-bottom: 0px;">
							<label for="first_name">Max Number:</label>
							<div class="form-group">
							  <div class="form-line">
							      <input type="text" class="form-control" v-model="maxTicketNumber">
							  </div>
							</div>
							<p class="col-teal" v-if="notifications.generate_notif">Ticket Generated!</p>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
							<button type="submit" class="btn btn-link waves-effect">
							<span v-if="!preloaders.generate_loader">GENERATE</span>
							<span v-if="preloaders.generate_loader">GENERATING...</span>
							</button>
							<button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
export default {
	data () {
		return {
			tickets: [],
			maxTicketNumber: 0,
			preloaders: {
				generate_loader: false
			},
			notifications: {
				generate_notif: false
			}
		}
	},
	mounted () {
		this.validateUser()
	},
	methods: {
		async resetQue () {
			try {
				let result = await app.service('kickstart').find()
				console.log(result)
			} catch (error) {
				console.log(error)
			}
		},
		async updateCounter () {
			try {
				let result = app.service('users').update({
					
				})
				console.log(result)
			} catch (error) {
				console.log(error)
			}
		},
		async editTicket (counter) {
			this.editTicketForm = counter
		},
		async validateUser () {
			try {
				let result = await isAuth()
				if (result.user.type !== 'superuser') {
					window.location.href = '/login'
				}
				this.getTickets()
			} catch (error) {
				console.log(error)
			}
		},
		async getTickets () {
			try {
				let result = await app.service('tickets').find()
				this.tickets = result.total
				console.log(result)
			} catch (error) {
				console.log(error)
			}
		},
		async generateTicket () {
			this.tickets = []

			this.preloaders.generate_loader = true
			try {
				let result = await app.service('generate-ticket').create({ ticketLength: this.maxTicketNumber})
				if (result.success) {
					this.notifications.generate_notif = true
					this.preloaders.generate_loader = false
					this.getTickets()
				}
			} catch (error) {
				console.log(error)
			}
		},
		async emptyFormData () {
			this.addTicketForm = {
				label: ''			
			}
		}
	}
}
</script>


