<template>
	<div id="counter">
		<div class="container" style="text-align: center; padding-top: 100px;">
			<h3 class="uk-card-title" style="font-size: 150px; font-weight: bold;">{{ currentTicket.ticket.label }}</h3>
			<p style="font-size: 40px; text-transform: uppercase;">{{ counterName }}</p>
			<select v-model="transaction" @change="saveTransaction">
				<option value="">Please choose transaction</option>
				<option v-for="t in transactionsArray">{{ t }}</option>
			</select>
			<p style="margin-top: 20px;"><button type="button" class="next-btn" @click="getNextTicket">NEXT TICKET</button></p>
		</div>
		<div>
			<button class="logout-btn" @click="logout">LOGOUT</button>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
const getNextTicketService = app.service('get-next-ticket')
export default {
	data () {
		return {
			currentTicket: {
				ticket: {
					label: 0
				}
			},
			user: '',
			counterName: '',
			ticketRetrieved: true,
			transaction: '',
			transactionsArray: [
				'OTR',
				'DIPLOMA',
				'CERTIFICATION OF GRADES',
				'EVALUATION OF GRADES',
				'HONORABLE DISMISSAL',
				'OTR PAYMENT'
			]
		}
	},
	async mounted () {
		let self = this
		window.addEventListener("keypress", function(e) {
      if (e.keyCode === 13) {
      	self.getNextTicket()
      }
    });
		try {
			let result = await isAuth()
			this.user = result.user
			this.getCounter()
		} catch (error) {
			window.location.href = '/login'
		}
	},
	methods: {
		async saveTransaction () {
			console.log(this.currentTicket)
			try	{
				let result = await app.service('now-serving').patch(this.currentTicket._id, {
					transaction: this.transaction
				})
				console.log(result)
			} catch (error) {
				console.log(error)
			}
		},
		async getCounter () {
			let result = await app.service('counters').find({
				query: {
					occupied_by: {
						_id: this.user._id
					}
				}
			})
			this.counterName = result.data[0].label
			this.retainTicket(result.data[0]._id)
		},
		async getNextTicket () {
			if (this.transaction) {
				try {
					let result = await app.service('get-next-ticket').create({transaction: this.transaction})
					this.currentTicket = result
					this.transaction = ''
					this.$forceUpdate()
				} catch (error) {
					console.log(error)
				}
			} else {
				alert('Please choose a transaction!')
			}

		},
		async retainTicket (counterID) {
			try {
				let result = await app.service('now-serving').find({
					query: {
						counter: counterID
					}
				})
				this.currentTicket = result.data[0]
			} catch (error) {
				console.log(error)
			}
		},
		async logout () {
			localStorage.removeItem('feathers-jwt')
			window.location.href = '/login'
		}
	}
}
</script>
<style scoped>
#counter {
	background-image: url('https://i.pinimg.com/originals/cd/c8/de/cdc8de777d6705a71853293304c5436f.jpg');
	height: 100vh;
}
.next-btn {
	padding: 10px;
	background: #049587;
	color: #fff;
	font-weight: bold;
	border-radius: 20px;
}
.logout-btn {
	position: absolute; 
	right: 0; 
	bottom: 0; 
	margin: 20px; 
	background: #F4453E; 
	padding: 10px; 
	border-radius: 20px;
	color: #fff;
	font-weight: bold;
}
.custom-select {
  position: relative;
  font-family: Arial;
}
.custom-select select {
  display: none; /*hide original SELECT element:*/
}
.select-selected {
  background-color: DodgerBlue;
}
/*style the arrow inside the select element:*/
.select-selected:after {
  position: absolute;
  content: "";
  top: 14px;
  right: 10px;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-color: #fff transparent transparent transparent;
}
/*point the arrow upwards when the select box is open (active):*/
.select-selected.select-arrow-active:after {
  border-color: transparent transparent #fff transparent;
  top: 7px;
}
/*style the items (options), including the selected item:*/
.select-items div,.select-selected {
  color: #ffffff;
  padding: 8px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
}
/*style items (options):*/
.select-items {
  position: absolute;
  background-color: DodgerBlue;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
/*hide the items when the select box is closed:*/
.select-hide {
  display: none;
}
.select-items div:hover, .same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}
</style>


