<template>
	<div id="settings">
		<section class="content">
			<div class="container-fluid">
			  <div class="block-header">
					<div class="row clearfix">
					    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					        <div class="card">
					            <div class="header">
					                <h2>
					                    SETTINGS
					                </h2>
					                <ul class="header-dropdown m-r--5">
					                    <li class="dropdown">
					                        <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
					                            <i class="material-icons">more_vert</i>
					                        </a>
					                        <ul class="dropdown-menu pull-right">
					                            <li><a data-toggle="modal" data-target="#updateSettings" class=" waves-effect waves-block">Update Settings</a></li>
					                        </ul>
					                    </li>
					                </ul>
					            </div>
					            <div class="body table-responsive">
					                <table class="table">
					                    <thead>
					                        <tr>
					                            <th style="width: 25%;">APPLICATION NAME</th>
					                            <th style="width: 75%;">ANNOUNCEMENT</th>
					                        </tr>
					                    </thead>
					                    <tbody>
					                        <tr v-for="(setting, index) in settings" :key="index">
				                            <td>{{ setting.company_name }}</td>
				                            <td>{{ setting.announcement }}</td>
					                        </tr>
					                    </tbody>
					                </table>
					            </div>
					        </div>
					    </div>
					</div>			  
				</div>
			</div>
		</section>
		<!-- Add Counter -->
		<div class="modal fade" id="updateSettings" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Update Settings</h4>
				  </div>
				  <form @submit.prevent="updateSettings">
					  <div class="modal-body" style="padding-bottom: 0px;">
							<label for="first_name">Application Name:</label>
							<div class="form-group">
							  <div class="form-line">
							      <input type="text" class="form-control" v-model="formData.company_name">
							  </div>
							</div>
							<div class="form-group">
								<div class="form-line">
								    <textarea rows="4" class="form-control no-resize" placeholder="Announcement" v-model="formData.announcement"></textarea>
								</div>
							</div>

							<p class="col-teal" v-if="notifications.add_notif">Settings Updated!</p>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
							<button type="submit" class="btn btn-link waves-effect">
							<span v-if="!preloaders.add_loader">UPDATE</span>
							<span v-if="preloaders.add_loader">UPDATING...</span>
							</button>
							<button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
export default {
	data () {
		return {
			settings: [],
			preloaders: {
				add_loader: false
			},
			notifications: {
				add_notif: false
			},
			formData: {
				company_name: '',
				announcement: ''
			}
		}
	},
	mounted () {
		this.getSettings()
	},
	methods: {
		async updateSettings () {
			this.preloaders.add_loader = true
			try {
				let result = await app.service('settings').create(this.formData)
				if (result) {
					this.preloaders.add_loader = false
					this.notifications.add_notif = true
				}
			} catch (error) {
				console.log(error)
			}
		},
		async getSettings () {
			try {
				let result = await app.service('settings').find()
				this.settings = result.data
				this.formData = result.data[0]
			} catch (error) {
				console.log(error)
			}
		}
	}
}
</script>


