<template>
  <div id="employees">
    <section class="content">
      <div class="container-fluid">
        <div class="block-header">
          <div class="row clearfix">
            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div class="card">
                <div class="header">
                  <h2>EMPLOYEES</h2>
                  <ul class="header-dropdown m-r--5">
                    <li class="dropdown">
                      <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
                        <i class="material-icons">more_vert</i>
                      </a>
                      <ul class="dropdown-menu pull-right">
                        <li><a data-toggle="modal" data-target="#addEmployee" class=" waves-effect waves-block">Add Employee</a></li>
                      </ul>
                    </li>
                  </ul>
                </div>
                <div class="body table-responsive">
                  <table class="table">
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>NAME</th>
                          <th>EMAIL</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(employee, index) in employees" :key="index">
                          <th scope="row">{{ index+1 }}</th>
                          <td style="text-transform: capitalize;">{{ employee.first_name + ' ' + employee.last_name }}</td>
                          <td>{{ employee.email }}</td>
                          <td>
                            <i class="material-icons" @click="editEmployee(employee)" data-toggle="modal" data-target="#editEmployee" style="cursor: pointer;">mode_edit</i>
                          </td>
                        </tr>
                      </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>        
        </div>
      </div>
    </section>

    <!-- Add Employee -->
    <div class="modal fade" id="addEmployee" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
              <h4 class="modal-title">Add Employee</h4>
          </div>
          <form @submit.prevent="saveEmployee">
            <div class="modal-body" style="padding-bottom: 0px;">
                <label for="first_name">First Name:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="text" class="form-control" v-model="addEmployeeForm.first_name" required>
                    </div>
                </div>
                <label for="last_name">Last Name:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="text" class="form-control" v-model="addEmployeeForm.last_name" required>
                    </div>
                </div>
                <label for="email">Email:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="email" class="form-control" v-model="addEmployeeForm.email" required>
                    </div>
                </div>
                <label for="password">Password:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="password" class="form-control" v-model="addEmployeeForm.password" required>
                    </div>
                </div>
                <p class="col-teal" v-if="notifications.add_notif">Employee Added!</p>
            </div>
            <div class="modal-footer" style="padding-top: 0px;">
                <button type="submit" class="btn btn-link waves-effect">
                <span v-if="!preloaders.add_loader">SAVE</span>
                <span v-if="preloaders.add_loader">SAVING...</span>
                </button>
                <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <!-- Edit Employee -->
    <div class="modal fade" id="editEmployee" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
              <h4 class="modal-title">Edit Employee</h4>
          </div>
          <form @submit.prevent="updateEmployee">
            <div class="modal-body" style="padding-bottom: 0px;">
                <label for="first_name">First Name:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="text" class="form-control" v-model="editEmployeeForm.first_name" required>
                    </div>
                </div>
                <label for="last_name">Last Name:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="text" class="form-control" v-model="editEmployeeForm.last_name" required>
                    </div>
                </div>
                <label for="email">Email:</label>
                <div class="form-group">
                    <div class="form-line">
                        <input type="email" class="form-control" v-model="editEmployeeForm.email" required>
                    </div>
                </div>
                <p class="col-teal" v-if="notifications.edit_notif">Employee Updated!</p>
            </div>
            <div class="modal-footer" style="padding-top: 0px;">
                <button type="submit" class="btn btn-link waves-effect">
                <span v-if="!preloaders.edit_loader">UPDATE</span>
                <span v-if="preloaders.edit_loader">UPDATING...</span>
                </button>
                <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
            </div>
          </form>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
export default {
  data () {
    return {
      employees: [],
      editEmployeeForm: {},
      addEmployeeForm: {
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        type: 'employee'
      },
      preloaders: {
        add_loader: false,
        edit_loader: false
      },
      notifications: {
        add_notif: false,
        edit_notif: false
      }
    }
  },
  mounted () {
    this.validateUser()
  },
  methods: {
    async updateEmployee () {
      this.preloaders.edit_loader = true
      try {
        let result = await app.service('users').patch(this.editEmployeeForm._id, this.editEmployeeForm)
        console.log(result)
        if (result.updated_at) {
          this.preloaders.edit_loader = false
          this.notifications.edit_notif = true
        }
      } catch (error) {
        console.log(error)
      }
    },
    async editEmployee (employee) {
      this.editEmployeeForm = employee
    },
    async validateUser () {
      try {
        let result = await isAuth()
        if (result.user.type !== 'superuser') {
          window.location.href = '/login'
        }
        this.getEmployees()
      } catch (error) {
        console.log(error)
      }
    },
    async getEmployees () {
      try {
        let result = await app.service('users').find({
          query: {
            type: 'employee'
          }
        })
        this.employees = result.data
      } catch (error) {
        console.log(error)
      }
    },
    async saveEmployee () {
      this.preloaders.add_loader = true
      try {
        let result = await app.service('users').create(this.addEmployeeForm)
        console.log(result.created_at)
        if (result.created_at) {
          this.preloaders.add_loader = false
          this.notifications.add_notif = true
          this.emptyFormData()
        }
      } catch (error) {
        if (error.className === 'conflict') {
          alert('Email is already taken.')
          this.preloaders.add_loader = false
        } else {
          alert(JSON.stringify(error))
        }
      }
    },
    async emptyFormData () {
      this.addEmployeeForm = {
        first_name: '',
        last_name: '',
        email: '',
        password: ''
      }
    }
  }
}
</script>


