<template>
  <div id="advertisements">
    <section class="content">
      <div class="container-fluid">
        <div class="block-header">
          <div class="row clearfix">
            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div class="card">
                <div class="header">

                  <h2>ADVERTISEMENTS</h2>

                  <ul class="header-dropdown m-r--5">
                    <li class="dropdown">
                      <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
                        <i class="material-icons">more_vert</i>
                      </a>
                      <ul class="dropdown-menu pull-right">
                        <li><a data-toggle="modal" data-target="#addAdvertisements" class=" waves-effect waves-block" data-backdrop="static" data-keyboard="false">Add New Advertisement</a></li>
                      </ul>
                    </li>
                  </ul>
                </div>
                <div class="body table-responsive">
                  <table class="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>NAME</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(ad, index) in advertisements" :key="index">
                        <th scope="row">{{ index+1 }}</th>
                        <td>{{ ad.name }}</td>
                          <i class="material-icons" data-toggle="modal" data-target="#editAdvertisements" style="cursor: pointer;" @click="setCurrentAdvertisement(ad)">mode_edit</i>
                          <i class="material-icons" style="cursor: pointer;"
                          @click="removeAdvertisement(ad)">delete</i>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>        
        </div>
      </div>
    </section>
    <!-- Create Advertisement -->
    <div class="modal fade" id="addAdvertisements" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">Add New Advertisement</h4>
          </div>
          <form @submit.prevent="addNewAdvertisement">
            <div class="modal-body" style="padding-bottom: 0px;">
              <label for="name">Name:</label>
              <div class="form-group">
                <div class="form-line">
                  <input type="text" class="form-control" v-model="advertisementData.name">
                </div>
              </div>

              <!-- Loading indicator -->
              <div style="margin-bottom: 10px;" v-if="isFileUploading">
                <div class="preloader pl-size-xs">
                  <div class="spinner-layer pl-red-grey">
                    <div class="circle-clipper left">
                      <div class="circle"></div>
                    </div>
                    <div class="circle-clipper right">
                      <div class="circle"></div>
                    </div>
                  </div>
                </div>
                <p >Uploading file. Please wait...</p>
              </div>

              <input type="file" @change="uploadFile">
            </div>
            <p v-if="notifications.add_notif" style="text-align: center; margin-top: 10px;" class="col-teal">Advertisement Added! You may now close this modal.</p>
            <div class="modal-footer" style="padding-top: 0px;">

              <button type="submit" class="btn btn-link waves-effect" :disabled="isFileUploading">
                <span v-if="!preloaders.add_loader">SAVE</span>
                <span v-if="preloaders.add_loader">SAVING...</span>
              </button>

              <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <!-- Edit Advertisement -->
    <div class="modal fade" id="editAdvertisements" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">Edit Advertisement</h4>
          </div>
          <form @submit.prevent="updateAdvertisement">
            <div class="modal-body" style="padding-bottom: 0px;">
              <label for="first_name">Name:</label>
              <div class="form-group">
                <div class="form-line">
                  <input type="text" class="form-control" v-model="advertisementData.name">
                </div>
              </div>
            </div>
            <p class="col-teal" v-if="notifications.edit_notif">Advertisement Updated!</p>
            <div class="modal-footer" style="padding-top: 0px;">
              <button type="submit" class="btn btn-link waves-effect">
                <span v-if="!preloaders.edit_loader">SAVE</span>
                <span v-if="preloaders.edit_loader">SAVING...</span>
              </button>              
              <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import { API_SERVER } from '@/api/url'
import moment from 'moment'

export default {
  data () {
    return {
      advertisementData: {},
      advertisements: [],
      currentFileUploadProgress: 0,
      preloaders: {
        add_loader: false,
        edit_loader: false
      },
      notifications: {
        add_notif: false,
        edit_notif: false
      },
      isFileUploading: false
    }
  },
  mounted () {
    this.validateUser()
  },
  methods: {
    async validateUser () {
      try {
        let result = await isAuth()
        if (result.user.type !== 'superuser') {
          window.location.href = '/login'
        }
        this.getAdvertisements()
      } catch (error) {
        console.log(error)
      }
    },
    async setCurrentAdvertisement (data) {
      this.advertisementData = data
    },
    async getAdvertisements () {
      try {
        let result = await app.service('advertisements').find()

        this.advertisements = result.data
      } catch (err) {
        console.log(err)
      } finally {

      }
    },
    async addNewAdvertisement () {
      this.preloaders.add_loader = true
      try {
        let result = await app.service('advertisements').create(this.advertisementData)
        console.log(result)
        if (result.created_at) {
          this.preloaders.add_loader = false
          this.notifications.add_notif = true
          this.advertisements.push(result)
          this.advertisementData = {}
          this.$forceUpdate()
        }

        alert('Success!')
      } catch (err) {
        if (err.message === 'advertisements validation failed: file_name: Path `file_name` is required.') {
          alert('Upload the advertisement file first!')
        }
      } finally {

      }
    },
    async updateAdvertisement () {
      try {
        let result = await app.service('advertisements').patch(
          this.advertisementData._id,
          this.advertisementData
        )

        this.advertisements.forEach((value, key) => {
          if (value._id === result._id) {
            value === result
          }
        })
        this.advertisementData = {}
        this.$forceUpdate()

        this.notifications.add_notif = true
      } catch (err) {
        console.log(err)
      }
    },
    async removeAdvertisement (ad) {
      try {
        let proceedDeleting = confirm('Are you sure you want to delete?')

        if (!proceedDeleting) return;

        let result = await app.service('advertisements').remove(ad._id)

        this.advertisements.forEach((value, key) => {
          if (value._id === result._id) {
            value === result
            this.advertisements.splice(key, 1)
          }
        })
        this.advertisementData = {}
        this.$forceUpdate()

        alert('Success!')
      } catch (err) {
        console.log(err)
      } finally {

      }
    },
    uploadFile (event) {
      this.isFileUploading = true
      this.currentFileUploadProgress = 0
      let file = event.target.files[0];
      let formData = new FormData();

      formData.append('uri', file);

      $.ajax({
        url: `${API_SERVER}/cloudinary`,
        cache: false,
        contentType: false,
        processData: false,
        data: formData,
        type: 'POST',
        success: response => {
          this.advertisementData.file_name = response.url
          this.isFileUploading = false
          alert('Upload complete.')
        },
        error: (error) => {
          console.log(error);
          this.isFileUploading = false
          alert('Something went wrong. Please double check your file type. (Use .mp4 format)')
        },
        xhr: function() {
          // create an XMLHttpRequest
          var xhr = new XMLHttpRequest();

          // listen to the 'progress' event
          xhr.upload.addEventListener('progress', (evt) => {
            if (evt.lengthComputable) {
              // calculate the percentage of upload completed
              var percentComplete = evt.loaded / evt.total;
              percentComplete = parseInt(percentComplete * 100);

              this.currentFileUploadProgress = percentComplete

              // once the upload reaches 100%, set the progress bar text to done
              if (percentComplete === 100) {
                // alert('Upload complete!')
              }
            }
          }, false);

          return xhr;
        } // xhr
      });
    } // uploadFile
  } // methods
}
</script>


