<template>
	<div id="counters">
		<section class="content">
			<div class="container-fluid">
			  <div class="block-header">
					<div class="row clearfix">
					    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					        <div class="card">
					            <div class="header">
					                <h2>
					                    COUNTERS
					                </h2>
					                <ul class="header-dropdown m-r--5" v-if="counters.length >= 4">
					                    <li class="dropdown">
					                        <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
					                            <i class="material-icons">more_vert</i>
					                        </a>
					                        <ul class="dropdown-menu pull-right">
					                            <li><a data-toggle="modal" data-target="#addCounter" class=" waves-effect waves-block">Add Counter</a></li>
					                        </ul>
					                    </li>
					                </ul>
					            </div>
					            <div class="body table-responsive">
					                <table class="table">
					                    <thead>
					                        <tr>
					                            <th>#</th>
					                            <th>COUNTER</th>
					                            <th>OCCUPIED BY</th>
					                        </tr>
					                    </thead>
					                    <tbody>
					                        <tr v-for="(counter, index) in counters" :key="index">
				                            <th scope="row">{{ index+1 }}</th>
				                            <td>{{ counter.label }}</td>
				                            <td><span v-if="counter.occupied_by">{{ counter.occupied_by.first_name + ' ' + counter.occupied_by.last_name }}</span><span v-else class="font-bold col-pink">NOT OCCUPIED</span><td>
				                            <td>
				                            	<i class="material-icons" @click="editCounter(counter)" data-toggle="modal" data-target="#editCounter" style="cursor: pointer;">mode_edit</i>
				                            </td>
					                        </tr>
					                    </tbody>
					                </table>
					            </div>
					        </div>
					    </div>
					</div>			  
				</div>
			</div>
		</section>
		<!-- Add Counter -->
		<div class="modal fade" id="addCounter" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Add Counter</h4>
				  </div>
				  <form @submit.prevent="saveCounter">
					  <div class="modal-body" style="padding-bottom: 0px;">
					  	<label for="name">Name:</label>
							<div class="form-group">
							  <div class="form-line">
							      <input type="text" class="form-control" v-model="addCounterForm.label">
							  </div>
							</div>
							<label for="occupier">Choose Occupier:</label>
							<div class="demo-radio-button">
								<div v-for="(employee, index) in employees" :key="index">
									<input name="addOccupier" type="radio" :id="index" class="with-gap radio-col-blue-grey" v-model="addCounterForm.occupied_by" :value="employee._id"/>
							  	<label :for="index">{{ employee.first_name + ' ' + employee.last_name }}</label>
								</div>
							</div>
							<p class="col-teal" v-if="notifications.add_notif">Counter Added!</p>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
							<button type="submit" class="btn btn-link waves-effect">
							<span v-if="!preloaders.add_loader">SAVE</span>
							<span v-if="preloaders.add_loader">SAVING...</span>
							</button>
							<button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
		<!-- Edit Counter -->
		<div class="modal fade" id="editCounter" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Edit Counter</h4>
				  </div>
				  <form @submit.prevent="updateCounter">
					  <div class="modal-body" style="padding-bottom: 0px;">
							  <label for="first_name">Name:</label>
							  <div class="form-group">
							      <div class="form-line">
							          <input type="text" class="form-control" v-model="editCounterForm.label">
							      </div>
							  </div>
							  <label for="occupier">Choose Occupier:</label>
								<div class="demo-radio-button">
									<div v-for="(employee, index) in employees" :key="employee._id">
										<input name="editOccupier" type="radio" :id="employee._id" class="with-gap radio-col-blue-grey" v-model="editCounterForm.occupied_by" :value="employee._id"/>
								  	<label :for="employee._id">{{ employee.first_name + ' ' + employee.last_name }}</label>
									</div>
								</div>
							  <p class="col-teal" v-if="notifications.edit_notif">Counter Updated!</p>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
					      <button type="submit" class="btn btn-link waves-effect">
					      <span v-if="!preloaders.edit_loader">UPDATE</span>
					      <span v-if="preloaders.edit_loader">UPDATING...</span>
					    	</button>
					      <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
export default {
	data () {
		return {
			counters: [],
			editCounterForm: {},
			addCounterForm: {
				label: '',
				occupied_by: ''
			},
			preloaders: {
				add_loader: false,
				edit_loader: false
			},
			notifications: {
				add_notif: false,
				edit_notif: false
			},
			employees: []
		}
	},
	mounted () {
		this.validateUser()
	},
	methods: {
		async setOccupier (employeeID) {
			this.addCounterForm.occupied_by = employeeID
		},
		async getEmployees () {
			try {
				let result = await app.service('counters').find()
				let empEmails = []
				result.data.forEach(val => {
					if (val.occupied_by) {
						empEmails.push(val.occupied_by.email)
					}
				})
				this.getAvailableEmployees(empEmails)
			} catch (error) {
				console.log(error)
			}
		},
		async getAvailableEmployees (empEmails) {
			try {
				let result = await app.service('users').find({
					query: {
						type: 'employee',
						email: {
							$nin: empEmails
						}
					}
				})
				this.employees = result.data
			} catch (error) {
				console.log(error)
			}
		},
		async updateCounter () {
			this.preloaders.edit_loader = true
			try {
				let result = await app.service('counters').update(this.editCounterForm._id, this.editCounterForm)
				console.log(result)
				if (result.updated_at) {
					this.preloaders.edit_loader = false
					this.notifications.edit_notif = true
				}
			} catch (error) {
				console.log(error)
			}
		},
		async editCounter (counter) {
			this.editCounterForm = counter
		},
		async validateUser () {
			try {
				let result = await isAuth()
				if (result.user.type !== 'superuser') {
					window.location.href = '/login'
				}
				this.getCounters()
				this.getEmployees()
			} catch (error) {
				console.log(error)
			}
		},
		async getCounters () {
			try {
				let result = await app.service('counters').find()
				this.counters = result.data
			} catch (error) {
				console.log(error)
			}
		},
		async saveCounter () {
			this.preloaders.add_loader = true
			try {
				let result = await app.service('counters').create(this.addCounterForm)
				if (result.created_at) {
					this.preloaders.add_loader = false
					this.notifications.add_notif = true
					this.emptyFormData()
				}
			} catch (error) {
				console.log(error)
			}
		},
		async emptyFormData () {
			this.addCounterForm = {
				label: ''			
			}
		}
	}
}
</script>


