<template>
	<div id="queues">
		<section class="content">
			<div class="container-fluid">
			  <div class="block-header">
					<div class="row clearfix">
					    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					        <div class="card">
					            <div class="header">
					                <h2>
					                    QUEUE
					                    <!-- <small>Basic example without any additional modification classes</small> -->
					                </h2>
					            </div>
					            <div class="body table-responsive">
					                <table class="table">
					                    <thead>
					                        <tr>
					                            <th>TICKET NUMBER</th>
					                            <th>TRANSACTION</th>
					                        </tr>
					                    </thead>
					                    <tbody>
					                        <tr v-for="(queue, index) in queues" :key="index">
				                            <td>{{ queue.ticket.label }}</td>
					                        </tr>
					                    </tbody>
					                </table>
					            </div>
					        </div>
					    </div>
					</div>			  
				</div>
			</div>
		</section>
		<!-- Add Counter -->
		<div class="modal fade" id="addCounter" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Add Ticket</h4>
				  </div>
				  <form @submit.prevent="saveTicket">
					  <div class="modal-body" style="padding-bottom: 0px;">
							<!-- <label for="first_name">Name:</label> -->
							<div class="form-group form-float">
							  <div class="form-line">
							      <input type="text" class="form-control" v-model="addQueueForm.label">
							      <label class="form-label">Name:</label>
							  </div>
							</div>
							<label for="last_name">Occupier:</label>
							<div class="form-group">
							  <div class="form-line">
							  	<select name="occupier" class="form-control">
							  		<option value="">1</option>
							  		<option value="">2</option>
							  	</select>
							      <!-- <input type="text" class="form-control" v-model="addQueueForm.last_name"> -->
							  </div>
							</div>
							<p class="col-teal" v-if="notifications.add_notif">Counter Added!</p>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
							<button type="submit" class="btn btn-link waves-effect">
							<span v-if="!preloaders.add_loader">SAVE</span>
							<span v-if="preloaders.add_loader">SAVING...</span>
							</button>
							<button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
		<!-- Edit Counter -->
		<div class="modal fade" id="editQueue" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Edit Ticket</h4>
				  </div>
				  <form @submit.prevent="updateCounter">
					  <div class="modal-body" style="padding-bottom: 0px;">
							  <label for="first_name">Name:</label>
							  <div class="form-group">
							      <div class="form-line">
							          <input type="text" class="form-control" v-model="editQueueForm.label">
							      </div>
							  </div>
							  <p class="col-teal" v-if="notifications.add_notif">Counter Added!</p>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
					      <button type="submit" class="btn btn-link waves-effect">
					      <span v-if="!preloaders.add_loader">SAVE</span>
					      <span v-if="preloaders.add_loader">SAVING...</span>
					    	</button>
					      <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
export default {
	data () {
		return {
			queues: [],
			editQueueForm: {},
			addQueueForm: {
				label: ''
			},
			preloaders: {
				add_loader: false
			},
			notifications: {
				add_notif: false
			}
		}
	},
	mounted () {
		this.validateUser()
	},
	methods: {
		async updateCounter () {
			try {
				let result = app.service('users').update({
					
				})
				console.log(result)
			} catch (error) {
				console.log(error)
			}
		},
		async editQueue (counter) {
			this.editQueueForm = counter
		},
		async validateUser () {
			try {
				let result = await isAuth()
				if (result.user.type !== 'superuser') {
					window.location.href = '/login'
				}
				this.getQueues()
			} catch (error) {
				console.log(error)
			}
		},
		async getQueues () {
			try {
				let result = await app.service('main-queue').find()
				this.queues = result.data
			} catch (error) {
				console.log(error)
			}
		},
		async saveTicket () {
			this.preloaders.add_loader = true
			try {
				let result = await app.service('queues').create(this.addQueueForm)
				console.log(result.created_at)
				if (result.created_at) {
					this.preloaders.add_loader = false
					this.notifications.add_notif = true
					this.emptyFormData()
				}
			} catch (error) {
				console.log(error)
			}
		},
		async emptyFormData () {
			this.addQueueForm = {
				label: ''			
			}
		}
	}
}
</script>


