<template>
	<div id="side-menu">
		<section>
        <!-- Left Sidebar -->
        <aside id="leftsidebar" class="sidebar">
            <!-- User Info -->
            <div class="user-info">
                <div class="image">
                    <img src="/static/images/user.png" width="48" height="48" alt="User" />
                </div>
                <div class="info-container">
                    <div class="name" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" style="text-transform: capitalize;">{{ user.first_name + ' ' + user.last_name }}</div>
                    <div class="email">{{ user.email }}</div>
                    <div class="btn-group user-helper-dropdown">
                        <i class="material-icons" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">keyboard_arrow_down</i>
                        <ul class="dropdown-menu pull-right">
                            <!-- <li><a href="javascript:void(0);"><i class="material-icons">person</i>Profile</a></li>
                            <li role="seperator" class="divider"></li>
                            <li><a href="javascript:void(0);"><i class="material-icons">group</i>Followers</a></li>
                            <li><a href="javascript:void(0);"><i class="material-icons">shopping_cart</i>Sales</a></li>
                            <li><a href="javascript:void(0);"><i class="material-icons">favorite</i>Likes</a></li>
                            <li role="seperator" class="divider"></li> -->
                            <li @click="logOut"><a href="javascript:void(0);"><i class="material-icons">input</i>Logout</a></li>
                        </ul>
                    </div>
                </div>
            </div>
            <!-- #User Info -->
            <!-- Menu -->
            <div class="menu">
                <ul class="list">
                    <li class="header">MAIN NAVIGATION</li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Employees' }">
                        <router-link to="/admin">
                            <i class="material-icons">supervisor_account</i>
                            <span>Employees</span>
                        </router-link>
                    </li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Counters' }">
                        <router-link to="/admin/counters">
                            <i class="material-icons">supervisor_account</i>
                            <span>Counters</span>
                        </router-link>
                    </li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Tickets' }">
                        <router-link to="/admin/tickets">
                            <i class="material-icons">credit_card</i>
                            <span>Tickets</span>
                        </router-link>
                    </li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Queues' }">
                        <router-link to="/admin/queues">
                            <i class="material-icons">queue</i>
                            <span>Queue</span>
                        </router-link>
                    </li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Advertisements' }">
                        <router-link to="/admin/advertisements">
                            <i class="material-icons">perm_media</i>
                            <span>Advertisements</span>
                        </router-link>
                    </li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Logs' }">
                        <router-link to="/admin/logs">
                            <i class="material-icons">credit_card</i>
                            <span>Logs</span>
                        </router-link>
                    </li>
                    <li @click="setActiveLink" :class="{ 'active': routeName === 'Settings' }">
                        <router-link to="/admin/settings">
                            <i class="material-icons">settings</i>
                            <span>Settings</span>
                        </router-link>
                    </li>
                </ul>
            </div>
            <!-- #Menu -->
            <!-- Footer -->
            <div class="legal">
                <div class="copyright">
                    &copy; 2018 <a href="javascript:void(0);">QMS</a>.
                </div>
                <div class="version">
                    <b>Version: </b> 1.0
                </div>
            </div>
            <!-- #Footer -->
        </aside>
        <!-- #END# Left Sidebar -->
    </section>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
export default {
    data () {
        return {
            user: {},
            routeName: ''
        }
    },
    mounted () {
        this.validateUser()
        this.routeName = this.$route.name
    },
    methods: {
        async validateUser () {
            try {
                let result = await isAuth()
                this.user = result.user
                if (result.user.type !== 'superuser') {
                    window.location.href = '/login'
                }
            } catch (error) {
                if (error.className === 'not-authenticated') {
                    window.location.href = '/login'
                } else {
                    console.log(error)
                }
            }
        },
        async setActiveLink () {
            this.routeName = this.$route.name
        },
        async logOut () {
            localStorage.removeItem('feathers-jwt')
            window.location.href = '/login'
        }
    }
}
</script>


