import Vue from 'vue'
import Router from 'vue-router'
import Monitor from '@/components/Monitor'
// Admin Pages
import Admin from '@/components/admin/Admin'
import Employees from '@/components/admin/pages/Employees'
import Counters from '@/components/admin/pages/Counters'
import Tickets from '@/components/admin/pages/Tickets'
import Queues from '@/components/admin/pages/Queues'
import Advertisements from '@/components/admin/pages/Advertisements'
import Logs from '@/components/admin/pages/Logs'
import Settings from '@/components/admin/pages/Settings'

import Login from '@/components/Login'
import Counter from '@/components/admin/Counters'
import Test from '@/components/Test'
import ChooseYourCounter from '@/components/ChooseYourCounter'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/test',
      component: Test
    },
    {
      path: '/',
      name: 'Monitor',
      component: Monitor
    },
    {
    	path: '/admin',
    	parentName: 'Admin',
    	component: Admin,
      children: [
        {
          path: '',
          name: 'Employees',
          component: Employees
        },
        {
          path: 'counters',
          name: 'Counters',
          component: Counters
        },
        {
          path: 'tickets',
          name: 'Tickets',
          component: Tickets
        },
        {
          path: 'queues',
          name: 'Queues',
          component: Queues
        },
        {
          path: 'advertisements',
          name: 'Advertisements',
          component: Advertisements
        },
        {
          path: 'logs',
          name: 'Logs',
          component: Logs
        },
        {
          path: 'settings',
          name: 'Settings',
          component: Settings
        }
      ]
    },
    {
    	path: '/login',
    	name: 'Login',
    	component: Login
    },
    {
    	path: '/counter',
    	name: 'Counter',
    	component: Counter
    },
    {
      path: '/choose-your-counter',
      name: 'Choose Your Counter',
      component: ChooseYourCounter
    }
  ],
  mode: 'history'
})



