<template>
  <div style="margin: 10px;">
    <h1>Testing Page</h1>

    <div class="testing-page-body uk-flex">
      <div>
        <p>Kick Start System</p>
        <button @click="kickStart">NOW!!!</button>
      </div>

      <div>
        <p>Main Queue</p>
        <li v-for="queue in mainQueue">{{ queue.ticket.label }}</li>
      </div>

      <div>
        <p>
          Add Ticket
          <span v-if="isLoading">(Adding...)</span>
        </p>
        Ticket Name: <input type="text" v-model="newTicket.label">
        <button @click="addTicket">Add</button>
      </div>

      <div>
        <p>Now Serving</p>
        <p>{{ nowServing }}</p>
        <button @click="serveNext">Serve Next</button>
      </div>
    </div>
  </div>
</template>

<script>
import app from '@/api'
import isAuth from '@/api/isAuth'

export default {
  data () {
    return {
      isLoading: false,
      newTicket: {
        label: ''
      },
      mainQueue: [],
      nowServing: null,
      user: null,
      counter: null
    }
  },
  async mounted () {
    let user = await isAuth().then(res => res.user)
    this.user = user

    let counter = await app.service('counters').find({
      query: {
        occupied_by: this.user._id
      }
    }).then(res => res.data[0])
    this.counter = counter
    console.log(this.counter)

    this.mainQueue = await app.service('main-queue').find({
      query: {
        $sort: {
          created_at: 1
        }
      }
    }).then(res => res.data)

    this.nowServing = await app.service('now-serving').find({
      query: {
        counter: this.counter._id
      }
    }).then(res => {
      if (res.total) {
        return res.data[0].ticket.label
      } else {
        return null
      }
    })
  },
  methods: {
    async addTicket () {
      if (this.newTicket.label === '') {
        alert('Type something!')
      }
      try {
        this.isLoading = true
          
        let result = await app.service('tickets').create(this.newTicket)
        console.log(result)
        this.newTicket.label = ''
      } catch (err) {
        alert(err.message)
      } finally {
        this.isLoading = false
      }
    },
    async kickStart () {
      try {
        // this.isLoading = true
        let previousTickets = await app.service('main-queue').find({
          query: {
            $sort: { created_at: -1 }
          }
        }).then(res => res.data)

        for (var i = 0; i < previousTickets.length; i++) {
          let ticket = previousTickets[i]
          await app.service('main-queue').remove(ticket._id)
        }       

        let previousTicketServing = await app.service('now-serving').find({
          query: {
            $sort: { created_at: -1 }
          }
        }).then(res => res.data)

        for (var i = 0; i < previousTicketServing.length; i++) {
          let data = previousTicketServing[i]
          await app.service('now-serving').remove(data._id)
        } 

        let newTickets = await app.service('tickets').find({
          query: {
            $sort: { created_at: 1 }
          }
        }).then(res => res.data)

        for (var i = 0; i < newTickets.length; i++) {
          let ticket = newTickets[i]
          await app.service('main-queue').create({
            ticket: ticket._id
          })
        } 

        alert('Kick Started!')
      } catch (err) {
        alert(err.message)
      } finally {
        // this.isLoading = false
      }
    },
    async serveNext () {
      try {
        // this.isLoading = true

        let doServeNext = async () => {
          let mainQueue = await app.service('main-queue').find({
            query: {
              $sort: { created_at: -1 }
            }
          }).then(res => res.data)

          let latestTicket = mainQueue[0]

          await app.service('main-queue').remove(latestTicket._id)

          await app.service('now-serving').create({
            ticket: latestTicket.ticket._id,
            counter: this.counter._id
          })

          this.nowServing = latestTicket.ticket.label
        }

        let ticketServing = await app.service('now-serving').find({
          query: {
            counter: this.counter._id
          }
        })

        alert(JSON.stringify(this.counter))

        // if (ticketServing.total === 0) {
        //   doServeNext()
        // } else {
        //   let data = ticketServing.data[0]

        //   await app.service('now-serving').remove(data._id)

        //   alert(JSON.stringify(data))

        //   await app.service('main-queue').create({
        //     ticket: data.ticket._id
        //   })

        //   doServeNext()
        // }

      } catch (err) {
        alert(err.message)
      } finally {
        // this.isLoading = false
      }
    },
  },
  feathers: {
    'now-serving': {
      created (ticket) {
        console.log(ticket)
      },
    }
  }
}
</script>

<style>
.testing-page-body > div {
  margin-right: 20px;
}
</style>


