<template>
	<div id="logs">
		<section class="content">
			<div class="container-fluid">
			  <div class="block-header">
					<div class="row clearfix">
					    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					        <div class="card">
					            <div class="header">
					                <h2>
					                    LOGS
					                    <!-- <small>Basic example without any additional modification classes</small> -->
					                </h2>
					            </div>
					            <div class="body table-responsive">
					                <table class="table">
					                    <thead>
					                        <tr>
					                            <th>TICKET #</th>
					                            <th>TRANSACTION</th>
					                            <th>DATE SERVED</th>
					                            <th></th>
					                        </tr>
					                    </thead>
					                    <tbody>
					                        <tr v-for="(log, index) in logs" :key="index">
				                            <td>{{ log.ticket.label }}</td>
				                            <td>{{ log.transaction }}</td>
				                            <td>{{ log.created_at | formatDate }}</td>
				                            <td>{{ log.counter.label }} - {{ log.counter.occupied_by.first_name + ' ' +  log.counter.occupied_by.last_name}}</td>
					                        </tr>
					                    </tbody>
					                </table>
					            </div>
					        </div>
					    </div>
					</div>			  
				</div>
			</div>
		</section>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
export default {
	data () {
		return {
			logs: []		
		}
	},
	mounted () {
		this.validateUser()
	},
	methods: {
		async validateUser () {
			try {
				let result = await isAuth()
				if (result.user.type !== 'superuser') {
					window.location.href = '/login'
				}
				this.getLogs()
			} catch (error) {
				console.log(error)
			}
		},
		async getLogs () {
			try {
				let result = await app.service('/backlog').find()
				console.log(result)
				this.logs = result.data
			} catch (error) {
				console.log(error)
			}
		}
	},
	filters: {
		formatDate (val) {
			return moment(val).format('lll')
		}
	}
}
</script>


